.menu {
    color: white;
    padding: 10px;
    text-align: center;
}

.menu button {
    border: 2px solid black; /* Black border with 2px thickness */
    border-radius: 10px; /* Rounded corners, adjust radius as needed */
    padding: 10px 15px; /* Padding inside the button */
    background-color: white; /* Button background color */
    color: black; /* Text color */
    font-size: 16px; /* Text font size */
    cursor: pointer; /* Cursor changes to a pointer to indicate it's clickable */
    transition: background-color 0.3s ease; /* Smooth transition for hover effect */
    margin: 10px;
}

.menu button:hover {
    background-color: lightgray; /* Color changes when hovering over the button */
}
